import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import "./MapContainer3.css";
import styled from "styled-components";
import Box from "@mui/material/Box";
import FormControl from "@mui/material/FormControl";
import NativeSelect from "@mui/material/NativeSelect";
import MarkerModal from "../components/MarkerModal";
import NavBar from "../components/NavBar";

import { history } from "../redux/configureStore";

//  
import Button from "../elements/Button";
import backward from "../image/backward.png";
import notification from "../image/Notification.png";
import map from "../image/map.png";

const { kakao } = window;
const MapContainer3 = () => {
  const [searchList, setSearchList] = useState();

  const [latitude, setLatitude] = useState();
  const [longitude, setLongitude] = useState();
  const [is_modal, setModal] = useState(false); //   
  const [placename, setPlacename] = useState();

  useEffect(() => {
    var markers = [];

    var mapContainer = document.getElementById("map"), //   ID
      mapOption = {
        center: new kakao.maps.LatLng(37.566826, 126.9786567), //  
        level: 1, //   
      };

    //   
    var map = new kakao.maps.Map(mapContainer, mapOption);

    //    
    var ps = new kakao.maps.services.Places();

    //    (,  )
    ps.keywordSearch(searchList, placesSearchCB);

    //     
    function placesSearchCB(data, status) {
      if (status === kakao.maps.services.Status.OK) {
        //   
        //    
        displayPlaces(data);
      } else if (status === kakao.maps.services.Status.ZERO_RESULT) {
        //   
        alert("   .");
        return;
      } else if (status === kakao.maps.services.Status.ERROR) {
        //   
        alert("    .");
        return;
      }
    }

    //      
    function displayPlaces(places) {
      var listEl = document.getElementById("placesList"),
        fragment = document.createDocumentFragment(),
        bounds = new kakao.maps.LatLngBounds();

      //      
      removeAllChildNods(listEl);

      //     
      removeMarker();

      for (var i = 0; i < 15; i++) {
        // ,  
        //    
        var placePosition = new kakao.maps.LatLng(places[i].y, places[i].x);
        var marker = addMarker(placePosition, i);
        var itemEl = getListItem(i, places[i]);
        kakao.maps.event.addListener(marker, "click", function () {
          setLatitude(latitude);
          setLongitude(longitude);
          setPlacename(placename);

          console.log(latitude, longitude);
          setModal(true);
        });
        console.log(places[i]);
        const latitude = places[i].y;
        const longitude = places[i].x;
        const placename = places[i].place_name; //    
        console.log(placePosition);
        //       
        // LatLngBounds   
        bounds.extend(placePosition);

        //    mouseover 
        //     
        // mouseout    

        (function (marker, title) {
          kakao.maps.event.addListener(marker, "mouseover", function () {
            displayInfowindow(marker, title);
          });

          kakao.maps.event.addListener(marker, "mouseout", function () {
            infowindow.close();
          });

          itemEl.onmouseover = function () {
            displayInfowindow(marker, title);
          };

          itemEl.onmouseout = function () {
            infowindow.close();
          };
        })(marker, places[i].place_name);

        fragment.appendChild(itemEl);
      }

      //       
      map.setBounds(bounds);
    }

    //   Element  
    function getListItem(index, places) {
      var el = document.createElement("div");
      // itemStr = '<span class="markerbg marker_' + (index+1) + '"></span>' +
      //             '<div class="info">' +
      //             '   <h5>' + places.place_name + '</h5>';

      // if (places.road_address_name) {
      //     itemStr += '    <span>' + places.road_address_name + '</span>' +
      //                 '   <span class="jibun gray">' +  places.address_name  + '</span>';
      // } else {
      //     itemStr += '    <span>' +  places.address_name  + '</span>';
      // }

      //   itemStr += '  <span class="tel">' + places.phone  + '</span>' +
      //             '</div>';

      // el.innerHTML = itemStr;
      // el.className = 'item';

      return el;
    }

    //       
    function addMarker(position, idx, title) {
      var imageSrc =
          "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/marker_number_blue.png", //   url,   
        imageSize = new kakao.maps.Size(36, 37), //   
        imgOptions = {
          spriteSize: new kakao.maps.Size(36, 691), //   
          spriteOrigin: new kakao.maps.Point(0, idx * 46 + 10), //       
          offset: new kakao.maps.Point(13, 37), //      
        },
        markerImage = new kakao.maps.MarkerImage(
          imageSrc,
          imageSize,
          imgOptions
        ),
        marker = new kakao.maps.Marker({
          position: position, //  
          image: markerImage,
        });

      marker.setMap(map); //    
      markers.push(marker); //    

      return marker;
    }
    //          
    var infowindow = new kakao.maps.InfoWindow({ zIndex: 1 });
    //       
    function removeMarker() {
      for (var i = 0; i < markers.length; i++) {
        markers[i].setMap(null);
      }
      markers = [];
    }

    //        
    //   
    function displayInfowindow(marker, title) {
      var content = '<div style="padding:5px;z-index:1;">' + title + "</div>";

      infowindow.setContent(content);
      infowindow.open(map, marker);
    }

    //    Element  
    function removeAllChildNods(el) {
      while (el.hasChildNodes()) {
        el.removeChild(el.lastChild);
      }
    }
  }, [searchList]);

  const closeModal = () => {
    setModal(false);
  };
  return (
    <Crapper>
      <TopWrap>
        <Button _onClick={() => history.goBack()}>
          <img
            src={backward}
            style={{
              width: "10px",
              height: "18px",
            }}
          />
        </Button>
        <TopTitle> </TopTitle>
        <Button>
          <img src={notification} style={{ width: "24px", height: "24px" }} />
        </Button>
      </TopWrap>

      <div
        id="map"
        style={{
          width: "390px",
          height: "600px",
        }}
      >
        <div id="menu_wrap" class="bg_white">
          <div class="option">
            <div>
              <form onsubmit="searchPlaces(); return false;">
                <Box sx={{ minWidth: 300 }}>
                  <FormControl sx={{ width: 300 }}>
                    <NativeSelect
                      sx={{ width: 300 }}
                      defaultValue=""
                      value={searchList}
                      onChange={(e) => {
                        setSearchList(e.target.value);
                      }}
                      inputProps={{
                        name: "age",
                        id: "uncontrolled-native",
                      }}
                    >
                      <option value=""> ?</option>
                      <option value=""></option>
                      <option value=""></option>
                      <option value=""> </option>
                      <option value=""> </option>
                      <option value=""> </option>
                      <option value=""> </option>
                      <option value="">
                         
                      </option>
                      <option value=""> </option>
                    </NativeSelect>
                  </FormControl>
                </Box>
                {/*  <input type="text" value={search} onChange={(e)=>setSearch(e.target.value)}id="keyword" size="10"/> 
                    <button onClick={handleSubmit}></button>  */}
              </form>
            </div>
          </div>
        </div>
        {is_modal ? (
          <MarkerModal
            close={closeModal}
            locationCategory={searchList}
            latitude={latitude}
            longitude={longitude}
            placename={placename}
          />
        ) : null}
      </div>

      <div id="placesList"></div>
      {/* <div id="pagination"></div> */}
      <NavBar />
    </Crapper>
  );
};

const Crapper = styled.div`
  margin: 0 auto;
  width: 390px;
  height: 600px;
  box-sizing: border-box;
`;

const TopWrap = styled.div`
  box-sizing: border-box;
  position: relative;
  padding: 30px 0;
  display: flex;
  justify-content: space-between;
`;
const TopTitle = styled.div`
  font-size: 18px;
  line-height: 52px;
`;

export default MapContainer3;
