import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { useDispatch } from "react-redux";
import { actionCreators as roadActions } from "../redux/modules/marker";
const { kakao } = window;
const Road = () => {
  const dispatch = useDispatch();
  const [info, setInfo] = useState();
  const [distance, setDistance] = useState();
  const [dot, setDot] = useState([]);
  console.log(info)
  console.log(dot)  //dot,info , 
  useEffect(() => {
    var mapContainer = document.getElementById('map'), //   div  
    mapOption = { 
        center: new kakao.maps.LatLng(37.52070852977449, 127.12256761236603), //  
        level: 3 //   
    };

var map = new kakao.maps.Map(mapContainer, mapOption); //  

var drawingFlag = false; //       
var moveLine; //          
var clickLine //      
var distanceOverlay; //     
var dots = {}; //            .
let positions = [];
console.log(dots)
//    
//           
kakao.maps.event.addListener(map, 'click', function(mouseEvent) {

    //    
    var clickPosition = mouseEvent.latLng;

    //       
    if (!drawingFlag) {

        //  true,    
        drawingFlag = true;
        
        //       
        deleteClickLine();
        
        //       
        deleteDistnce();

        //              
        deleteCircleDot();
    
        //       
        clickLine = new kakao.maps.Polyline({
            map: map, //    
            path: [clickPosition], //       
            strokeWeight: 3, //   
            strokeColor: '#db4040', //  
            strokeOpacity: 1, //   0 1  0  
            strokeStyle: 'solid' //  
        });
        console.log([clickPosition])
        //             
        moveLine = new kakao.maps.Polyline({
            strokeWeight: 3, //   
            strokeColor: '#db4040', //  
            strokeOpacity: 0.5, //   0 1  0  
            strokeStyle: 'solid' //      
        });
         console.log(clickLine)
        //      
        displayCircleDot(clickPosition, 0);
        // setDot([...dot,clickPosition])
            
    } else { //    

        //      
        var path = clickLine.getPath();

        //     
        path.push(clickPosition);
        
        //          
        clickLine.setPath(path);
         console.log(clickPosition)
        var distance = Math.round(clickLine.getLength());
        displayCircleDot(clickPosition, distance);
        // setDot([...dot,clickPosition])
    }
});
    
//    
//            
kakao.maps.event.addListener(map, 'mousemove', function (mouseEvent) {

    //       
    if (drawingFlag){
        
        //      
        var mousePosition = mouseEvent.latLng; 

        //       
        var path = clickLine.getPath();
        
        //           
        var movepath = [path[path.length-1], mousePosition];
        moveLine.setPath(movepath);    
        moveLine.setMap(map);
        
        var distance = Math.round(clickLine.getLength() + moveLine.getLength()), //    
            content = '<div class="dotOverlay distanceInfo"> <span class="number">' + distance + '</span>m</div>'; //   
        
        //   
        showDistance(content, mousePosition);   
    }             
});                 

//      
//           
kakao.maps.event.addListener(map, 'rightclick', function (mouseEvent) {

    //        
    if (drawingFlag) {
        
        //     
        moveLine.setMap(null);
        moveLine = null;  
        
        //       
        var path = clickLine.getPath();
    
        //     2 
        if (path.length > 1) {

            //         
            if (dots[dots.length-1].distance) {
                dots[dots.length-1].distance.setMap(null);
                dots[dots.length-1].distance = null;    
            }
            setDot(positions)
            var distance = Math.round(clickLine.getLength()), //    
                content = getTimeHTML(distance); //   
                setInfo(distance);
            //     
            showDistance(content, path[path.length-1]);  
             
        } else {

            //     1  
            //       .
            deleteClickLine();
            deleteCircleDot(); 
            deleteDistnce();

        }
        
        //  false,     
        drawingFlag = false;          
    }  
});    

//      
function deleteClickLine() {
    if (clickLine) {
        clickLine.setMap(null);    
        clickLine = null;        
    }
}

//        
//                
function showDistance(content, position) {
    
    if (distanceOverlay) { //   
        
        //      
        distanceOverlay.setPosition(position);
        distanceOverlay.setContent(content);
        
    } else { //     
        
        //     
        distanceOverlay = new kakao.maps.CustomOverlay({
            map: map, //   
            content: content,  //   
            position: position, //   .
            xAnchor: 0,
            yAnchor: 0,
            zIndex: 3  
        });      
    }
}

//      
//           
function deleteDistnce () {
    if (distanceOverlay) {
        distanceOverlay.setMap(null);
        distanceOverlay = null;
    }
}

//         
//     (   )  
function displayCircleDot(position, distance) {

    //       
    var circleOverlay = new kakao.maps.CustomOverlay({
        content: '<span class="dot"></span>',
        position: position,
        zIndex: 1
    });
 
    //  
    circleOverlay.setMap(map);
    
    if (distance > 0) {
        //          
        var distanceOverlay = new kakao.maps.CustomOverlay({
            content: '<div class="dotOverlay"> <span class="number">' + distance + '</span>m</div>',
            position: position,
            yAnchor: 1,
            zIndex: 2
        });

        //  
        distanceOverlay.setMap(map);
    }
    positions.push(position)
    setDot(positions)
    console.log(positions)
    //  
    dots.push({circle:circleOverlay, distance: distanceOverlay});
    console.log(dots)
}

//     (   )    
function deleteCircleDot() {
    var i;

    for ( i = 0; i < dots.length; i++ ){
        if (dots[i].circle) { 
            dots[i].circle.setMap(null);
        }

        if (dots[i].distance) {
            dots[i].distance.setMap(null);
        }
    }

    dots = [];
}

//         
//       ,   
// HTML Content   
function getTimeHTML(distance) {

    //    4km/h    67m/min
    var walkkTime = distance / 67 | 0;
    var walkHour = '', walkMin = '';

    //    60    
    if (walkkTime > 60) {
        walkHour = '<span class="number">' + Math.floor(walkkTime / 60) + '</span> '
    }
    walkMin = '<span class="number">' + walkkTime % 60 + '</span>'

    //    16km/h      267m/min
    var bycicleTime = distance / 227 | 0;
    var bycicleHour = '', bycicleMin = '';

    //    60    
    if (bycicleTime > 60) {
        bycicleHour = '<span class="number">' + Math.floor(bycicleTime / 60) + '</span> '
    }
    bycicleMin = '<span class="number">' + bycicleTime % 60 + '</span>'

    //   ,    HTML Content  
    var content = '<ul class="dotOverlay distanceInfo">';
    content += '    <li>';
    content += '        <span class="label"></span><span class="number">' + distance + '</span>m';
    content += '    </li>';
    content += '    <li>';
    content += '        <span class="label"></span>' + walkHour + walkMin;
    content += '    </li>';
    content += '    <li>';
    content += '        <span class="label"></span>' + bycicleHour + bycicleMin;
    content += '    </li>';
    content += '</ul>'
    setDistance(distance)
    return content;

  }}, []);

  return (
    <div>
      <Wrap id="map"></Wrap>
      <button onClick={()=>dispatch(roadActions.addRoadAX(dot,distance))}  > </button>
    </div>
  );
};

const Wrap = styled.div`
  width: 1000px;
  height:1200px;
  padding-bottom: 100%;
  box-sizing: border-box;
  border-radius: 20px;
`;

export default Road;
