// reducer
export default handleActions(
  {
    [GET_ALL]: (state, action) =>
      produce(state, (draft) => {
        draft.main = action.payload.main;
      }),
    [GET_OLYMPIC]: (state, action) =>
      produce(state, (draft) => {
        draft.olympic = action.payload.olympic;
      }),
    [GET_SEOUL]: (state, action) =>
      produce(state, (draft) => {
        draft.seoul = action.payload.seoul;
      }),
    [GET_BANPO]: (state, action) =>
      produce(state, (draft) => {
        draft.banpo = action.payload.banpo;
      }),
    [GET_MAP]: (state, action) =>
      produce(state, (draft) => {
        draft.map = action.payload.map;
      }),
    [GET_MY_POST]: (state, action) =>
      produce(state, (draft) => {
        draft.myList = action.payload.myList;
      }),
    [GET_POST]: (state, action) =>
      produce(state, (draft) => {
        draft.list = action.payload.list;
      }),
    [ADD_POST]: (state, action) =>
      produce(state, (draft) => {
        draft.list.push(action.payload.list);
      }),
    [UPDATE_POST]: (state, action) =>
      produce(state, (draft) => {
        draft.list = { ...draft.list, ...action.payload.post };
      }),
    [DELETE_POST]: (state, action) =>
      produce(state, (draft) => {
        console.log(action.payload.postId);
        draft.list = draft.list.filter(
          (post) => post.id !== action.payload.postId
        );
      }),
    [LOADING]: (state, action) =>
      produce(state, (draft) => {
        draft.is_loading = action.payload.is_loading;
      }),
    [GET_MODAL]: (state, action) =>
      produce(state, (draft) => {
        draft.modal = action.payload.modal;
      }),
  },
  initialState
);
const actionCreators = {
  getAll,
  getOlympic,
  getSeoul,
  getBanpo,
  getPost,
  getMyPost,
  addPost,
  updatePost,
  deletePost,

  modalMD,
  getAllMD,
  getOlympicMD,
  getSeoulMD,
  getBanpoMD,
  getMyPostMD,
  getPostMD,
  addPostMD,
  deletePostMD,
  updatePostMD,
  getMapMD,
};
export { actionCreators };
