// DogStaMain.js -    ( )
import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { useHistory } from "react-router";
import { useSelector, useDispatch } from "react-redux";

// 
import TopBar from "../../components/TopBar";
import NavBar from "../../components/NavBar";

// 
import { actionCreators as dogstaActions } from "../../redux/modules/dogsta";

//  + 
import dog from "../../image/dog.png";

const DogStaMain = (props) => {
  const dispatch = useDispatch();
  const history = useHistory();

  const postList = useSelector((state) => state.dogsta.mainList); //    
  const postLike = useSelector((state) => state.dogsta.mainLikeList); //    

  const [status, setStatus] = useState(); // ,   1
  const [focus, setFocus] = useState(); // ,   1     

  //     status   
  const newest = () => {
    setStatus("newest");
  };

  //     status   
  const mostLiked = () => {
    setStatus("mostLiked");
  };

  useEffect(() => {
    setStatus("newest");
    setFocus("newest");
    dispatch(dogstaActions.getAllPostMD()); //    
    dispatch(dogstaActions.getLikePostMD()); //   
  }, []);

  return (
    <Wrap>
      {/*     */}
      <TopBar>
        <TopBarImg src={dog} />
        <span></span>
      </TopBar>

      {/* ,   +    */}
      <Head>
        <Category>
          <button
            onClick={newest}
            onFocus={() => setFocus("newest")}
            style={{
              borderBottom: focus === "newest" ? "4px solid red" : "",
              color: focus === "newest" ? "#ff5656" : "#000",
            }}
          >
            
          </button>
          <button
            onClick={mostLiked}
            onFocus={() => setFocus("mostLiked")}
            style={{
              borderBottom: focus === "newest" ? "" : "4px solid red",
              color: focus === "newest" ? "#000" : "#ff5656",
            }}
          >
            
          </button>
        </Category>
      </Head>

      {/*   - , */}
      {status === "newest" ? (
        //  
        <Body>
          {/*    */}
          {postList.length == 0 ? (
            <>
              <NoCard>  . .</NoCard>
              <Button
                onClick={() => {
                  history.push("/dogStaWrite");
                }}
              >
                 
              </Button>
            </>
          ) : (
            <Posts>
              {postList.map((post, index) => {
                return (
                  <Card key={index}>
                    {/*   */}
                    <ImageWrap>
                      <CardImage
                        src={post.dogPostImage}
                        onClick={() =>
                          history.push(
                            `/dogStaDetail/${post.userId}/${post.dogPostId}`
                          )
                        }
                      />
                    </ImageWrap>

                    {/*   */}
                    <PostInfo>
                      <WriterInfo
                        onClick={() => {
                          history.push(`/mypage/${post.userId}`);
                        }}
                      >
                        <UserImage src={post.userImage} />
                        <span>{post.userNickname}</span>
                      </WriterInfo>
                      <LikeInfo>
                        <span>like</span>
                        {post.count}
                      </LikeInfo>
                    </PostInfo>
                  </Card>
                );
              })}
            </Posts>
          )}

          {/*     */}
          <NavBar add_dogsta />
        </Body>
      ) : (
        //  
        <Body>
          {/*    */}
          {postList.length == 0 ? (
            <>
              <NoCard>  . .</NoCard>
              <Button
                onClick={() => {
                  history.push("/dogStaWrite");
                }}
              >
                 
              </Button>
            </>
          ) : (
            <Posts>
              {postLike.map((post, index) => {
                return (
                  <Card key={index}>
                    {/*   */}
                    <ImageWrap>
                      <CardImage
                        src={post.dogPostImage}
                        onClick={() =>
                          history.push(
                            `/dogStaDetail/${post.userId}/${post.dogPostId}`
                          )
                        }
                      />
                    </ImageWrap>

                    {/*   */}
                    <PostInfo>
                      <WriterInfo
                        onClick={() => {
                          history.push(`/mypage/${post.userId}`);
                        }}
                      >
                        <UserImage src={post.userImage} />
                        <span>{post.userNickname}</span>
                      </WriterInfo>
                      <LikeInfo>
                        <span>like</span>
                        {post.count}
                      </LikeInfo>
                    </PostInfo>
                  </Card>
                );
              })}
            </Posts>
          )}

          {/*     */}
          <NavBar add_dogsta />
        </Body>
      )}
    </Wrap>
  );
};

const Wrap = styled.div`
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  margin: auto;
  padding: 0 5%;
`;
const NoCard = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 60px;
  border-radius: 20px;
`;
const Button = styled.button`
  display: flex;
  justify-content: center;
  width: 160px;
  padding: 16px 0;
  margin: 30px auto;
  background-color: #fff;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
  cursor: pointer;
  border-radius: 10px;
  border: 1px gray;
`;
const TopBarImg = styled.img`
  width: 24px;
  height: 24px;
  margin: -4px 10px;
`;
const Head = styled.div`
  text-align: center;
  margin-bottom: 32px;
`;
const Category = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  button {
    box-sizing: border-box;
    width: 70px;
    margin: 0 10px;
    padding-bottom: 10px;
    background-color: transparent;
    border: none;
    text-align: center;
    cursor: pointer;
  }
`;
const Body = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;
const Posts = styled.div`
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 48% 48%;
  row-gap: 20px;
  justify-content: space-between;
  width: 100%;
  cursor: pointer;
`;
const Card = styled.div`
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
`;
const ImageWrap = styled.div`
  width: 100%;
  position: relative;
  padding-bottom: 100%;
  overflow: hidden;
`;
const CardImage = styled.img`
  width: 100%;
  height: 100%;
  position: absolute;
  object-fit: cover;
  border-radius: 10px 10px 0 0;
`;
const PostInfo = styled.div`
  display: flex;
  justify-content: space-between;
  padding: 6px 10px 8px 10px;
`;
const WriterInfo = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  span {
    font-size: 14px;
  }
`;

const UserImage = styled.img`
  box-sizing: border-box;
  background-position: center;
  background-repeat: no-repeat;
  object-fit: cover;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin-right: 4px;
`;

const LikeInfo = styled.div`
  margin-right: 4px;
  span {
    display: inline-block;
    font-size: 14px;
    color: #888;
    margin: 0 4px 2px 0;
  }
`;

export default DogStaMain;
